@import "./../styles/variables.scss";
.cards{
    width: 100%;
   
    @media (max-width:768px) {
        &__flex{
            flex-direction: column ;
            justify-content: center;
            //margin:0;
            align-items: center;
        }
        &__img{
            width:250px;
            height: 255px;
            object-fit: cover;
            align-items: center;
            
        }
        &__text{
            margin: 0px;
            font-size:16px ;
        }
        
    }
    &__flex{
        display: flex;
        justify-content: center;
        width: 100%;
      
    }
    &__text{
        margin: 50px;
        padding-top: 55px;
        font-size:16px ;
    }
  
    &__img{
        width:250px;
        height: 250px;
        object-fit: cover;
       // margin:50px;
        //padding: 50px;
        padding:5px;
        align-items: center;
        align-content: center;
        justify-content: center;
        @include breakpoint(md) {
            width:500px;
            height: 500px;
            padding:55px
        }
    

    }
}